import React from 'react';
import './Audio.css';

import play_img from '../../../assets/images/play.png';
import pause_img from '../../../assets/images/pause.png';

class Audio extends React.Component {
    constructor() {
        super()
        this.state = {
            play: false,
            left: 0,
            draggable: false,
        }
        this.play = this.play.bind(this);
        this.handleMouseDown = this.handleMouseDown.bind(this);
        this.handleMouseMove = this.handleMouseMove.bind(this);
        this.handleMouseUp = this.handleMouseUp.bind(this);
        this.handleRangeClick = this.handleRangeClick.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }

    componentDidMount() {
    }

    componentWillReceiveProps(nextProps) {
        const {index} = this.props
        if(this.props.currentAudioIndex !== undefined && nextProps.currentAudioIndex !== index) {
            this.setState({
                left: 0,
                play: false,
            })
            const audio = document.getElementById(`audio${index}`);
            audio.currentTime = 0;
            audio.pause();
        }
    }

    play() {
        const {index} = this.props;
        this.props.setCurrentAudioIndex(index);
        const {play} = this.state;
        const audio = document.getElementById(`audio${index}`);
        play ? audio.pause() : audio.play();
        let timer = 0;
        let {range} = this.refs;
        let maxWidth = range.clientWidth;
        if(!play) {
            timer = setInterval(() => {
                let rangePercents = audio.currentTime/audio.duration;
                rangePercents = rangePercents.toFixed(2);
                this.setState({
                    left: maxWidth * rangePercents,
                })
                if(audio.currentTime === audio.duration){
                    audio.pause()
                    audio.currentTime=0;
                    clearInterval(timer);
                    this.setState({
                        left: 0,
                        play: false,
                    })
                }
            }, 1000)
        }else {
            clearInterval(timer);
        }
        this.setState({
            play: !play
        })
    }

    handleMouseDown(e) {
        this.setState({
            draggable: true,
        })
    }

    handleMouseMove(e) {
        if(!this.state.draggable) return false;
        const {index} = this.props;
        const audio = document.getElementById(`audio${index}`);
        let {range} = this.refs;
		let rangeLeft = range.getBoundingClientRect().left;
        let clientLeft = e.pageX;
		let maxWidth = range.clientWidth;
        let left = clientLeft - rangeLeft;
		if(left < 0) left = 0;
		if(left > maxWidth) left = maxWidth;
		let rangePercents = left/maxWidth;
        rangePercents = rangePercents.toFixed(2);
        audio.currentTime = audio.duration * rangePercents;
		this.setState({left})
    }

    handleMouseUp(e) {
        this.setState({
            draggable: false,
        })
    }

    handleMouseLeave(e) {
        this.setState({
            draggable: false,
        })
    }

    handleRangeClick(e) {
        const {index} = this.props;
        const audio = document.getElementById(`audio${index}`);
        let {range} = this.refs;
		let rangeLeft = range.getBoundingClientRect().left;
		let clientLeft = e.clientX;
		let maxWidth = range.clientWidth;
        let left = clientLeft - rangeLeft;
		if(left < 0) left = 0;
		if(left > maxWidth) left = maxWidth;
		let rangePercents = left/maxWidth;
        rangePercents = rangePercents.toFixed(2);
        audio.currentTime = audio.duration * rangePercents;
		this.setState({left})
    }

    render() {
        const { src, index } = this.props;
        const { play, left } = this.state;
        const playImg = play ? pause_img : play_img;
        return <div>
            <audio src={src} id={`audio${index}`}></audio>
            <div className="play-bar">
                <div
                    className="play-button"
                    style={{
                        background: `url(${playImg}) center center/100% no-repeat`,
                        backgroundSize: play ? '12px 16px' : '14px 16px',
                    }}
                    onClick={this.play}
                ></div>
                <div className="control-bar" ref="range" onClick={this.handleRangeClick}>
                    <div className="bg-bar"></div>
                    <div 
                        className="cur-bar"
                        style={{
                            width: left,
                        }}
                    ></div>
                    <div 
                        className="cur-btn"
                        style={{
                            left: left,
                        }}
                        onMouseDown={this.handleMouseDown}
                        onMouseMove={this.handleMouseMove}
                        onMouseUp={this.handleMouseUp}
                        onMouseLeave={this.handleMouseLeave}
                    ></div>
                </div>
            </div>
        </div>
    }
}

export default Audio;


