import React, {Component} from 'react';
import {connect} from 'react-redux';
import md5 from 'blueimp-md5';
import axios from 'axios';
import Cookies from 'js-cookie';

import { generateSign, app_key, app_secret, version, host } from '../config';
// import jsonp from 'jsonp';
import login_bg from '../../assets/images/login_bg.png';
import Modal from '../common/Modal';



const mapStateToProps = state => {
    return { articles: state.articles };
}
class ConnectedLogin extends Component {
    
    constructor() {
        super()
        this.state = {
            username: '',
            password: '',
            modalInfo: '',
            modalShow: false,
        }
        this.handleUsernameChange = this.handleUsernameChange.bind(this);
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.modalClose = this.modalClose.bind(this);
    }

    handleUsernameChange(e) {
        this.setState({
            username: e.target.value
        })
    }

    handlePasswordChange(e) {
        this.setState({
            password: e.target.value
        })
    }

    handleLoginClick() {
        const { username, password } = this.state;
        if(!username || !password) {
            this.setState({
                modalInfo: '账号或者密码不能为空',
                modalShow: true,
            })
        }
        const time = new Date().valueOf();
        const md5_password = md5(password);
        const action = 'USER-LOGIN';
        const from = 'web';

        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token:''
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: `${host}/service/user/user-login.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                account: username,
                password: md5_password,
                app_key,
                time,
                version,
                sign,
                from,
            }
        }).then((response) => {
            if(response.data.code===200) {
                const HALFDAY = 1/2;
                Cookies.set('userToken', response.data.data.userToken, {
                    expires: HALFDAY,
                })
                Cookies.set('userInfo', response.data.data, {
                    expires: HALFDAY,
                })
                window.location.href='/music'
            }else if(response.data.code !== 200 && response.data.message){
                this.setState({
                    modalInfo: response.data.message,
                    modalShow: true,
                })
            }else {
                this.setState({
                    modalInfo: '登录似乎有点问题，请稍后再试！',
                    modalShow: true,
                })
            }
        }).catch(e => {
            console.log('e', e)
        })
        /*
        jsonp('http://192.168.0.101:8088/service/user/user-login.htm', null, (err, data) => {
            if(err) {
                console.log(JSON.stringify(err.message));
            }else {
                console.log('data', JSON.stringify(data));
            }
        })
        */
    }

    modalClose() {
        this.setState({
            modalInfo: '',
            modalShow: false,
        })
    }

    render() {
        return (<div
                style={{
                    width: '311px',
                    height: '456px',
                    background: `url(${login_bg}) center top/100% no-repeat`,
                    backgroundColor: '#2c2e3a',
                    borderRadius: '10px',
                    padding: '10px',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'flex-end',
                    alignItems: 'center',
                }}
            >
                <div
                    style={{
                        color: '#fff',
                        marginTop: '5px',
                    }}
                >
                    <div
                        style={{
                            marginBottom: '10px',
                        }}
                    >唔哩账号</div>
                    <input
                        style={{
                            marginBottom: '15px',
                            backgroundColor: '#23252f',
                            borderRadius: '5px',
                            outline: 'none',
                            width: '200px',
                            border: 'none',
                            height: '30px',
                            lineHeight: '30px',
                            padding: '0 10px',
                            boxSizing: 'border-box',
                            color: '#fff',
                            fontSize: '12px',
                        }}
                        onChange={this.handleUsernameChange}
                    />
                    <div
                        style={{
                            marginBottom: '10px'
                        }}
                    >密码</div>
                    <input
                        type="password"
                        style={{
                            marginBottom: '25px',
                            backgroundColor: '#23252f',
                            borderRadius: '5px',
                            outline: 'none',
                            width: '200px',
                            border: 'none',
                            height: '30px',
                            lineHeight: '30px',
                            padding: '0 10px',
                            boxSizing: 'border-box',
                            color: '#fff',
                            fontSize: '12px',
                        }}
                        onChange={this.handlePasswordChange}
                    />
                    <div>
                        <div
                            style={{
                                width: '200px',
                                height: '30px',
                                borderRadius: '5px',
                                backgroundColor: '#ffd900',
                                color: '#fff',
                                fontSize: '16px',
                                textAlign: 'center',
                                lineHeight: '30px',
                                cursor: 'pointer',
                                userSelect: 'none',
                                marginBottom: '40px',
                            }}
                            onClick={this.handleLoginClick}
                        >登陆</div>
                    </div>
                </div>
                <Modal 
                    show={this.state.modalShow}
                    info={this.state.modalInfo}
                    close={this.modalClose}
                />
            </div>
        )
    }
}
    

const Login = connect(mapStateToProps)(ConnectedLogin);

export default Login;


