import md5 from 'blueimp-md5';

export const generateSign = (options) => {
    const origArr = Object.keys(options);
    const sortedArr = origArr.sort();
    const arr = sortedArr.map(item => {
        return `${item}=${options[item]}`
    })
    const origSign = arr.join('&');
    return md5(origSign);
}

