import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import store from './js/redux/store/index';
import Cookies from 'js-cookie';
import './index.css';

import MainLayout from './js/components/MainLayout';

import AuthorizedRoute from './js/components/AuthorizedRoute';
import PrimaryLayout from './js/components/Index';
import UnauthorizedLayout from './js/components/LoginPage';
import LoginPage from './js/components/LoginPage';

const userToken = Cookies.get('userToken');
const logged = !!userToken;

ReactDOM.render(
    <Provider store={store}>
        <BrowserRouter>
            <Switch>
                <Route exact path="/login" component={LoginPage} />
                <AuthorizedRoute path="/" logged={logged} component={PrimaryLayout} />
            </Switch>
        </BrowserRouter>
    </Provider>, 
    document.getElementById('root')
);



