import React, { Component } from 'react';
import './MusicBox.css';

import Audio from './Audio';

import { generateSign, app_key, app_secret, version, host } from '../../config';
import Modal from '../../common/Modal';

import Cookies from 'js-cookie';
import axios from 'axios';
import md5 from 'blueimp-md5';

import up from '../../../assets/images/up.png';
import down from '../../../assets/images/down.png';
import gray_up from '../../../assets/images/gray_up.png';

const Title = ({ onDelete }) => (
    <div
        style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
        }}
    >
        <div className="music-box-title">
            已上传歌曲
        </div>
        <div 
            className="music-box-delete"
            onClick={onDelete}
        >
            删除
        </div>
    </div>
)

const Header = ({selectedAll, handleSelectedAll}) => (
    <div className="music-box-header">
        <div className="music-name header-item">
            <div className="checkbox">
                <input 
                    type="checkbox"
                    id="myCheck"
                    checked={selectedAll}
                    onChange={handleSelectedAll}
                />
                <label htmlFor="myCheck"></label>
            </div>
            歌曲名
        </div>
        <div className="header-item singer">演唱者</div>
        <div className="header-item control">操作</div>
        <div className="header-item size">大小</div>
        <div className="header-item upload-time">上传时间</div>
    </div>
)

const Line = ({ 
    index, 
    selected, 
    name,
    singer,
    publishTime,
    size,
    address,
    handleSelect,
    setCurrentAudioIndex, 
    currentAudioIndex 
}) => (
    <div className="music-box-line">
        <div className="music-name body-item">
            <div className="checkbox">
                <input 
                    type="checkbox" 
                    id={`myCheck${index}`}
                    checked={selected}
                    onChange={handleSelect(index)}
                />
                <label htmlFor={`myCheck${index}`}></label>
            </div>
            <div className="music-name-text" title={name.length > 7 ? name : ''}>
                {name}
            </div>
        </div>
        <div className="body-item singer" title={singer.length > 6 ? singer : ''}>{singer}</div>
        <div className="body-item control">
            <Audio 
                src={address} 
                id="audio" 
                index={index} 
                setCurrentAudioIndex={setCurrentAudioIndex} 
                currentAudioIndex={currentAudioIndex}
            />
        </div>
        <div className="body-item size">{size}M</div>
        <div className="body-item upload-time">{publishTime}</div>
    </div>
)

const Pagination = ({
    handleMidButtonClick, 
    handleCurrentPageChange,
    handlePrevClick,
    handleNextClick,
    pageSelectShow,
    currentPage,
    totalSize,
    nextSearchStart,
}) => {
    const selectedStyle = {
        borderRadius: '0 0 5px 5px',
    }
    const arrowBgSelectedStyle = {
        backgroundColor: '#ffd900'
    }
    const pageSize = 20;
    const pages = totalSize && Math.ceil(totalSize/pageSize);
    const midBtnStyle = pageSelectShow && pages > 1 ? selectedStyle : null;
    const arrow =  pages > 1 ? pageSelectShow ? down : up : gray_up;
    const arrowBgStyle = pages > 1 ? arrowBgSelectedStyle : null;
    const pagesArr = pages && Array.apply(null, Array(pages)).map((item, index) => index + 1)
    return (
        <div className="music-box-pagination">
            <div 
                className="page-button"
                onClick={handlePrevClick(currentPage)}
            >上一页</div>
            <div className="page-mid">
                <div 
                    className="page-mid-select"
                    style={{
                        display: pageSelectShow && pages > 1 ? 'flex' : 'none',
                    }}
                >
                    {
                        pagesArr && pagesArr.map((item,index) => {
                            return currentPage !== item ? 
                            <span 
                                className="page-mid-option"
                                key={index}
                                onClick={handleCurrentPageChange(item)}
                            >{item}/{pages}页</span> :
                            ''
                        })
                    }
                </div>
                <div 
                    className="page-mid-btn"
                    onClick={handleMidButtonClick}
                    style={midBtnStyle}
                >
                    <span className="page-mid-option">{currentPage}/{pages}页</span>
                    <span 
                        className="arrow-box"
                        style={arrowBgStyle}
                    >
                        <span
                            style={{
                                width: '18px',
                                height: '14px',
                                background: `url(${arrow}) center center/50% no-repeat`,
                            }}
                        >
                        </span>
                    </span>
                </div>
            </div>
            <div 
                className="page-button"
                onClick={handleNextClick(currentPage, pages)}
            >下一页</div>
        </div>
    )
}

class MusicBox extends Component {
    constructor() {
        super()
        this.state ={
            selectedAll: false,
            lines: [],
            pageSelectShow: false,
            currentAudioIndex: undefined,
            currentPage: 1,
            modalInfo: '',
            modalShow: false,
            deleted: 0,
        }
        this._renderLines = this._renderLines.bind(this);
        this.handleSelectedAll = this.handleSelectedAll.bind(this);
        this.handleMidButtonClick = this.handleMidButtonClick.bind(this);
        this.setCurrentAudioIndex = this.setCurrentAudioIndex.bind(this);
        this.onDelete = this.onDelete.bind(this);
        this.modalClose = this.modalClose.bind(this);
    }

    componentDidMount() {
    }

    componentWillReceiveProps(nextProps, nextState) {
        if(nextProps.tableData !== this.props.tableData) {
            let lines = nextProps.tableData.results.map(item=>{
                item.selected = false;
                return item;
            })
            this.setState({
                lines
            })
        }
    }

    setCurrentAudioIndex(index) {
        this.setState({
            currentAudioIndex: index,

        })
    }

    _renderLines() {
        const { lines, currentAudioIndex } = this.state;
        return lines.map((item, index) => {
            const { selected, name, singer, publishTime, size, musicId, address } = item;
            const lineProps = {
                key: index,
                index,
                selected,
                name,
                singer,
                publishTime,
                size,
                musicId,
                address,
                handleSelect: this.handleSelect,
                setCurrentAudioIndex: this.setCurrentAudioIndex,
                currentAudioIndex,
            };
            return <Line {...lineProps} />;
        })
    }

    handleSelectedAll() {
        const lines = this.state.lines.map((item, index) => {
            item.selected = !this.state.selectedAll;
            return item;
        })        
        this.setState({
            selectedAll: !this.state.selectedAll,
            lines,
        })
    }

    handleSelect = (index) => (e) => {
        const { selectedAll } = this.state;
        const lines = this.state.lines.slice();
        lines[index].selected = !lines[index].selected;
        const update = {lines};
        const selectedLines = lines.filter((item, index) => {
            return item.selected;
        })
        const len = selectedLines.length;
        if(len === lines.length && !selectedAll){
            update.selectedAll = true;
        } 
        if(len !== lines.length && selectedAll) {
            update.selectedAll = false;
        }
        this.setState(update)
    }

    handleMidButtonClick() {
        this.setState({
            pageSelectShow: !this.state.pageSelectShow,
        })
    }

    onDelete() {
        const {lines} = this.state;
        const shouldDelete = lines.filter((item, index) => {
            return item.selected
        })
        if(shouldDelete.length === 0) {
            this.setState({
                modalInfo: '您没有选中的歌曲',
                modalShow: true,
            })
            return false;
        }
        const userToken = Cookies.get('userToken');
        const action = "MUSIC-REMOVE";
        const time = new Date().valueOf();
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: userToken,
        })
        const sign = md5(orig_sign + time);
        shouldDelete.map(async (item, index) => {
            const result = await this.removeMusic(item, sign, time, userToken, from, action);
            if(result.data.code === 200) {
                this.setState({
                    deleted: this.state.deleted + 1,
                }, () => {
                    if(shouldDelete.length - 1 === index) {
                        this.setState({
                            modalShow: true,
                            modalInfo: `成功删除了${this.state.deleted}首歌曲`,
                            deleted:  0,
                        })
                        this.props.getTableData();
                    }
                })
            }
            
        })
    }

    removeMusic(item, sign, time, userToken, from, action) {
        const { musicId, uid } = item;
        return axios({
            method: 'post',
            url: `${host}/service/music/music-remove.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                app_secret,
                time,
                version,
                user_token: userToken,
                uid,
                music_id: musicId,
                sign,
                from,
            }
        })
    }

    handleCurrentPageChange = (page) => (e) => {
        this.props.getTableData(page)
        this.setState({
            currentPage: page,
            pageSelectShow: false,
        })
    }

    handlePrevClick = (currentPage) => (e) => {
        const prevPage = currentPage - 1;
        if(prevPage > 0) {
            this.props.getTableData(prevPage);
            this.setState({
                currentPage: prevPage
            })
        }
    }

    handleNextClick = (currentPage, totalPage) => (e) => {
        const nextPage = currentPage + 1;
        if(nextPage <= totalPage) {
            this.props.getTableData(nextPage);
            this.setState({
                currentPage: nextPage
            })
        }
    }

    modalClose() {
        this.setState({
            modalInfo: '',
            modalShow: false,
        })
    }

    render() {
        const { selectedAll, pageSelectShow, currentPage } = this.state;
        const { tableData } = this.props;
        const titleProps = {
            onDelete: this.onDelete
        }
        const headerProps = {
            handleSelectedAll: this.handleSelectedAll,
            selectedAll,
        }
        const paginationProps = {
            handleMidButtonClick: this.handleMidButtonClick,
            handleCurrentPageChange: this.handleCurrentPageChange,
            handlePrevClick: this.handlePrevClick,
            handleNextClick: this.handleNextClick,
            pageSelectShow,
            currentPage,
            totalSize: tableData && tableData.totalSize, 
            nextSearchStart: tableData && tableData.nextSearchStart,
        }

        return (
            <div
                style={{
                    width: '684px',
                    minHeight: '450px',
                    borderRadius: '10px',
                    backgroundColor: '#2c2e3a',
                    padding: '16px',
                    paddingRight: '25px',
                    paddingBottom: '30px',
                }}
            >
                <Title {...titleProps} />
                {
                    this.state.lines.length !== 0 ?
                    <div>
                        <Header {...headerProps} />
                        <div className="music-box-table-body">
                            {this._renderLines()}
                        </div>
                    </div> : 
                    <div
                        style={{
                            borderRadius: '10px',
                            height: '360px',
                            backgroundColor: '#23252f',
                            fontsize: '16px',
                            color: '#999',
                            marginTop: '15px',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}
                    >歌曲库空荡荡的，快点上传一首歌曲吧</div>
                }
                <Pagination {...paginationProps} />
                <Modal 
                    show={this.state.modalShow}
                    info={this.state.modalInfo}
                    close={this.modalClose}
                />
            </div>
        )
    }
}

export default MusicBox;


