import React from 'react';
import Cookies from 'js-cookie';
import axios from 'axios';
import md5 from 'blueimp-md5';
import { generateSign, app_key, app_secret, version, host } from '../config';
import UserCard from './music/UserCard';
import MusicBox from './music/MusicBox';

class MusicList extends React.Component {
    constructor(props) {
        super(props)
        this.state={
            userInfo: null,
            userToken: '',
        }
        this.getTableData = this.getTableData.bind(this);
    }

    componentWillMount() {
        const userToken = Cookies.get('userToken');
        if(!userToken) window.location.href = '/login';        
    }

    componentDidMount() {
        this.getTableData()
    }

    shouldComponentUpdate() {
        const userToken = Cookies.get('userToken');
        if(!userToken && !this.state.modalShow) {
            this.setState({
                modalShow: true,
                modalInfo: '登录超时，请刷新页面后重新登录',
            })
            setTimeout(()=>{
                window.location.href = '/login'
            }, 3000)
            return false;
        }
        return true;
    }

    getTableData(search_start) {
        const userToken = Cookies.get('userToken');
        const userInfo = Cookies.getJSON('userInfo');
        const action = "MUSIC-RETRIEVE";
        const time = new Date().valueOf();
        const uid = userInfo && userInfo.uid;
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: userToken,
        })
        const sign = md5(orig_sign + time);
        search_start = search_start || 1
        axios({
            method: 'post',
            url: `${host}/service/music/music-retrieve.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                app_secret,
                time,
                version,
                user_token: userToken,
                uid,
                sign,
                search_start,
                from,
            }
        })
        .then((response) => {
            const {data} = response;
            if(data.code === 200) {
                const tableData = data.data
                this.setState({
                    tableData
                })
            }
        })
        .catch((e) => {
            console.log('e', e);
        })
    }

    render() {
        const {tableData} = this.state;
        const userInfo = Cookies.getJSON('userInfo');
        return (
            <div
                style={{
                    height: 'calc(100vh - 160px)',
                    width: 'calc(100vw + 30px)',
                    paddingTop: '15px',
                    paddingBottom: '15px',
                    overflowY: 'scroll',
                    overflowX: 'hidden',
                    
                }}
            >
                <div
                    style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                <UserCard 
                    userInfo={userInfo}
                    tableData={tableData}                    
                />
                <MusicBox
                    tableData={tableData}
                    getTableData={this.getTableData}
                />
                </div>
            </div>
        )
    }
}

export default MusicList


