import React from 'react';
import close from '../../assets/images/x.png';

class Modal extends React.Component {

    render() {
        return  (
            <div
                style={{
                    width: '100vw',
                    height: '100vh',
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    zIndex: 2,
                    backgroundColor: 'rgba(0,0,0,.5)',
                    display: this.props.show ? 'flex' : 'none',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}
            >
                <div
                    style={{
                        width: '505px',
                        height: '250px',
                        borderRadius: '10px', 
                        backgroundColor: '#2c2e3a',
                    }}
                >
                    <div
                        style={{
                            height: '50px',
                            display: 'flex',
                            justfifyContent: 'space-between',
                            alignItems: 'center',
                        }}
                    >
                        <div
                            style={{
                                width: '16px',
                                height: '16px',
                                background: `url(${close}) center center/100% no-repeat`,
                                marginLeft: 'auto',
                                marginRight: '16px',
                                cursor: 'pointer',
                            }}
                            onClick={this.props.close}
                        >
                        </div>
                    </div>
                    <div
                        style={{
                            height: '200px',
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'center',
                            alignItems: 'center',
                            color: '#fff',
                        }}
                    >
                        <div
                            style={{
                                fontSize: '16px',
                            }}
                        >
                            {this.props.info}
                        </div>
                        <div
                            style={{
                                width: '125px',
                                height: '35px',
                                borderRadius: '18px',
                                backgroundColor: '#ffd900',
                                fontSize: '16px',
                                textAlign: 'center',
                                lineHeight: '35px',
                                cursor: 'pointer',
                                userSelect: 'none',
                                marginTop: '55px',
                            }}
                            onClick={this.props.close}
                        >
                            确定
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Modal


