import React, { Component } from 'react';
import Main from './Main';
import Title from './Title';
import logo from '../../assets/images/logo.png';

class Index extends Component {
  constructor() {
    super();
    this.state={

    }
    this.handleLogoClick = this.handleLogoClick.bind(this);
  }
  
  handleLogoClick() {
    window.location.href = '/'
  }

  render() {
    return (
      <div>
        <div
          style={{
            backgroundColor: '#2c2e3a',
            height: '65px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 1,
          }}
        >
          <div 
            style={{
              width: '1040px',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
          >
            <div
              style={{
                width: '117px',
                height: '35px',
                background: `url(${logo}) center center/100%`,
                cursor: 'pointer',
                suerSelect: 'none',
              }}
              onClick={this.handleLogoClick}
            ></div>
            <Title />
          </div>
        </div>
        <div
          style={{
            height: 'calc(100vh - 130px)',
            backgroundColor: '#1b1c24',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            overflowY: 'scroll',
            overflowX: 'hidden',
          }}
        >
          {/* <Main /> */}
          {this.props.children}
        </div>
        <div
          style={{
            backgroundColor: '#2c2e3a',
            height: '65px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            color: '#999',
            zIndex: 1,
            fontSize: '12px',
          }}
        >浙ICP备 17036599号-1 杭州咕唧信息科技有限公司 邮箱：developer@guji-inc.com</div>
      </div>
    );
  }
}

export default Index;



