import React from 'react';
import { Switch, Route } from 'react-router-dom';

const Music = () => (
    <div
        style={{
            color: '#ffd900',
            fontWeight: 'bold',
            fontSize: '12px',
        }}
    >音乐上传</div>
)

const Title = () => (
    <div>
      <Switch>
        <Route exact path='/music' component={Music}/>
        <Route exact path='/upload' component={Music}/>
      </Switch>
    </div>
  )
  
  export default Title


