import React, {Component} from 'react';
import Cookies from 'js-cookie';
import { generateSign, app_key, app_secret, version, host } from '../../config';
import axios from 'axios';
import md5 from 'blueimp-md5';


class UserCard extends Component {
    constructor() {
        super()
        this.state = {
        }
        this.handleUploadMusicClick = this.handleUploadMusicClick.bind(this);
        this.logout = this.logout.bind(this);
    }

    handleUploadMusicClick() {
        window.location.href="/upload"
    }

    logout() {
        const user_token = Cookies.get('userToken');
        const {uid} = Cookies.getJSON('userInfo');
        const time = new Date().valueOf();
        const action = 'USER-LOGOUT';
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token,
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: `${host}/service/user/user-logout.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                user_token,
                uid,
                time,
                sign,
                version,
                from,
            }
        })
        .then(response => {
            console.log('logout response', response);
            if(response.data.code === 200) {
                Cookies.remove('userToken');
                Cookies.remove('userInfo');
                window.location.href = '/';
            }else {
               alert('登出失败！请稍后再试！');
            }
        })
        .catch(e => {
            console.log('e', e)
        })
    }

    render() {
        const { userInfo, tableData } = this.props;
        const photo = userInfo && userInfo.photo;
        return (
            <div
                style={{
                    width: '684px',
                    height: '100px',
                    color: '#fff',
                    fontSize: '12px',
                    backgroundColor: '#2c2e3a',
                    borderRadius: '5px',
                    marginBottom: '15px',
                    paddingLeft: '16px',
                    paddingRight: '25px',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                }}
            >
                <div
                    style={{
                        display: 'flex',
                    }}
                >
                    <div
                        style={{
                            width: '62px',
                            height: '62px',
                            borderRadius: '31px',
                            marginRight: '10px',
                            border: '1px solid #fff',
                            background: `url(${photo}) center center/100% no-repeat`,
                        }}
                    >
                    </div>
                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'space-around',
                            alignItems: 'flex-start',
                            height: '62px',
                        }}
                    >
                        <div
                            style={{
                                display: 'flex',
                                alignItems: 'center',
                            }}
                        >
                            <span
                                style={{
                                    fontSize: '16px',
                                }}
                            >{userInfo && userInfo.nickName}</span>
                            <span
                                style={{
                                    color: '#ffd900',
                                    fontSize: '12px',
                                    textDecoration: 'underline',
                                    cursor: 'pointer',
                                    userSelect: 'none',
                                    marginLeft: '10px',
                                }}
                                onClick={this.logout}
                            >退出当前账号</span>
                        </div>
                        
                        <span
                            style={{
                                color: '#999',
                            }}
                        >唔理号：{userInfo && userInfo.uid}</span>
                    </div>
                </div>
                <div
                    style={{
                        height: '62px',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'space-around',
                        alignItems:'flex-end',
                    }}
                >
                    <span>已上传歌曲：{tableData && tableData.totalSize}/100首</span>
                    <div
                        style={{
                            width: '100px',
                            height: '25px',
                            textAlign: 'center',
                            lineHeight: '25px',
                            borderRadius: '13px',
                            backgroundColor: '#ffd900',
                            cursor: 'pointer',
                            userSelect: 'none',
                        }}
                        onClick={this.handleUploadMusicClick}
                    >上传歌曲</div>
                </div>
            </div>
        )
    }
}

export default UserCard;


